import React from 'react';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './_footer.scss';

function footer() {
	library.add(fab);
	return (
		<div className='Footer'>
			<FontAwesomeIcon className='Footer__html5' icon={['fab', 'html5']} />
			<FontAwesomeIcon className='Footer__css3' icon={['fab', 'css3-alt']} />
			<FontAwesomeIcon className='Footer__sass' icon={['fab', 'sass']} />
			<FontAwesomeIcon className='Footer__js' icon={['fab', 'js']} />
			<FontAwesomeIcon className='Footer__react' icon={['fab', 'react']} />
			<FontAwesomeIcon className='Footer__npm' icon={['fab', 'npm']} />
			<FontAwesomeIcon className='Footer__git' icon={['fab', 'git-alt']} />
		</div>
	);
}

export default footer;
