import React, { memo } from 'react';
import './_frontPage.scss';
import Typing from 'react-typing-animation';
import developer from './img/developer.gif';
import Quotes from './quotes';

function frontPage() {
	return (
		<div className='Header'>
			<div className='Header-context'>
				<img
					className='Header-context__gif'
					src={developer}
					alt='developer gif'
				/>
				<Typing className='Header-context__text' loop={true}>
					<span className='Header-context__typewriter'>
						Self-taught <Typing.Speed ms={200} />
						Developer
					</span>
					<Typing.Delay ms={1500} />
					<Typing.Speed ms={50} />
					<Typing.Backspace count={25} />
					<span className='Header-context__typewriter'>
						Front-end <Typing.Speed ms={200} />
						Developer
					</span>
					<Typing.Delay ms={1500} />
					<Typing.Speed ms={50} />
					<Typing.Backspace count={25} />
				</Typing>
			</div>
			<Quotes className='Header-repos' />
		</div>
	);
}

export default memo(frontPage);
