import React from 'react';
import { NavLink } from 'react-router-dom';
import './_Navbar.scss';
import HomeOutlinedIcon from '@material-ui/icons/HomeOutlined';
import AccountCircleOutlinedIcon from '@material-ui/icons/AccountCircleOutlined';
import AssignmentOutlinedIcon from '@material-ui/icons/AssignmentOutlined';
import SmsOutlinedIcon from '@material-ui/icons/SmsOutlined';
import logo from './img/logo.jpg';

function navbar() {
	return (
		<div className='Navbar'>
			<div className='Navbar__logo'>
				<img src={logo} alt='logo' />
			</div>
			<div className='Navbar__links'>
				<NavLink exact to='/' className='Navbar-Link Navbar-Home'>
					<HomeOutlinedIcon style={{ width: '5rem', height: '5rem' }} />
				</NavLink>
				<NavLink exact to='/about' className='Navbar-Link Navbar-About'>
					<AccountCircleOutlinedIcon
						style={{ width: '5rem', height: '5rem' }}
					/>
				</NavLink>
				<NavLink className='Navbar-Link Navbar-Project'>
					<AssignmentOutlinedIcon style={{ width: '5rem', height: '5rem' }} />
				</NavLink>
				<NavLink className='Navbar-Link Navbar-Contact'>
					<SmsOutlinedIcon style={{ width: '5rem', height: '5rem' }} />
				</NavLink>
			</div>
		</div>
	);
}

export default navbar;
